html,
body {
  width  : 100%;
  height : 100%;
}

body {
  overflow-x : hidden;
}

.app {
  height     : auto;
  min-height : 100%;
  width      : 100%;
  position   : relative;
  &:before {
    content          : '';
    position         : absolute;
    width            : inherit;
    top              : 0;
    bottom           : 0;
    z-index          : -1;
    background-color : $body-bg;
    border           : inherit;
    display          : block;
  }
}

.app-header-fixed {
  padding-top : $app-header-height;
  .app-header {
    position : fixed;
    top      : 0;
    width    : 100%;
  }
}

.app-header {
  z-index       : 1025;
  border-radius : 0;
}

// menu
.app-aside {
  float : left;
  &:before {
    content          : '';
    position         : absolute;
    width            : inherit;
    top              : 0;
    bottom           : 0;
    z-index          : -1;
    background-color : inherit;
    border           : inherit;
  }
}

.app-aside-footer {
  position  : absolute;
  bottom    : 0;
  width     : 100%;
  z-index   : 1000;
  max-width : $app-aside-width;
  .app-aside-folded & {
    max-width : $app-aside-folded-width;
  }
  ~ div {
    padding-bottom : 50px;
  }
}

.app-aside-right {
  padding-bottom : $app-header-height;
}

// content
.app-content {
  height : 100%;
  @include clearfix();
}

.app-content-full {
  position                   : absolute;
  //top: $app-header-height;
  /*bottom: $app-footer-height;*/
  height                     : auto;
  width                      : auto !important;
  padding                    : 0 !important;
  overflow-y                 : auto;
  -webkit-overflow-scrolling : touch;
  &.h-full {
    bottom : 0;
    height : auto;
  }
}

.app-content-body {
  //padding-bottom: $app-header-height - 30px;
  float : left;
  width : 100%;
}

// footer
.app-footer {
  position : absolute;
  bottom   : 0;
  left     : 0;
  right    : 0;
  z-index  : 1005;
  &.app-footer-fixed {
    position : fixed;
  }
}

.hbox {
  display        : table;
  table-layout   : fixed;
  border-spacing : 0;
  width          : 100%;
  height         : 100%;
  .col {
    overflow       : hidden;
    display        : table-cell;
    vertical-align : top;
    height         : 100%;
    float          : none;
  }
}

.v-middle {
  vertical-align : middle !important;
}

.v-top {
  vertical-align : top !important;
}

.v-bottom {
  vertical-align : bottom !important;
}

.vbox {
  display        : table;
  border-spacing : 0;
  position       : relative;
  width          : 100%;
  height         : 100%;
  min-height     : 240px;
  .row-row {
    display : table-row;
    height  : 100%;
    .cell {
      position                   : relative;
      height                     : 100%;
      width                      : 100%;
      -webkit-overflow-scrolling : touch;
      overflow                   : auto;
      .ie & {
        display : table-cell;
      }
      .cell-inner {
        position : absolute;
        top      : 0;
        bottom   : 0;
        left     : 0;
        right    : 0;
      }
    }
  }
}

// navbar

.navbar {
  .navbar-form-sm {
    margin-top    : 10px;
    margin-bottom : 10px;
  }
  border-width  : 0;
  border-radius : 0;
  margin        : 0;
  padding       : 0;
}

.navbar-md {
  min-height : $app-header-md-height;
  .navbar-btn {
    margin-top : 13px;
  }
  .navbar-form {
    margin-top : 15px;
  }
  .navbar-nav > li > a {
    padding-top    : 20px;
    padding-bottom : 20px;
  }
  .navbar-brand {
    line-height : 60px;
  }
}

.navbar-header {
  > button {
    text-decoration  : none;
    line-height      : 30px;
    font-size        : 16px;
    padding          : 10px 17px;
    border           : none;
    background-color : transparent;
  }
}

.navbar-brand {
  float       : none;
  text-align  : center;
  font-size   : 20px;
  font-weight : 700;
  height      : auto;
  line-height : 45px;
  display     : inline-block;
  padding     : 0 20px;
  &:hover {
    text-decoration : none;
  }
  img {
    max-height     : 20px;
    margin-top     : -4px;
    vertical-align : middle;
    display        : inline;
  }
}

@media (min-width : 768px) {
  .app-aside {
    width : $app-aside-width;
  }

  .navbar-collapse,
  .app-content,
  .app-footer {
    margin-left : $app-aside-width;
  }

  .app-aside-right {
    position : absolute;
    top      : $app-header-height;
    bottom   : 0;
    right    : 0;
    z-index  : 1000;
    &.pos-fix {
      z-index : 1010;
    }
  }

  .visible-folded {
    display : none;
  }

  .app-aside-folded {
    .hidden-folded {
      display : none !important;
    }
    .visible-folded {
      display : inherit;
    }
    .text-center-folded {
      text-align : center;
    }
    .pull-none-folded {
      float : none !important;
    }
    .w-auto-folded {
      width : auto;
    }

    .app-aside,
    .navbar-header {
      width : $app-aside-folded-width;
    }
    .navbar-collapse,
    .app-content,
    .app-footer {
      margin-left : $app-aside-folded-width;
    }
    .app-header {
      .navbar-brand {
        display : block;
        padding : 0;
      }
    }
  }

  .app-aside-fixed {
    .app-header {
      .navbar-header {
        position : fixed;
      }
    }
    .aside-wrap {
      position         : fixed;
      overflow         : hidden;
      //top: $app-header-height;
      top              : 0;
      bottom           : 0;
      left             : 100px;
      width            : $app-aside-width - 100px;
      z-index          : 1050;
      background-color : #222c3c;
      .navi-wrap {
        width                      : $app-aside-width + $scroll-bar-width;
        //width: $app-aside-width - 100;
        position                   : relative;
        height                     : 100%;
        overflow-x                 : hidden;
        overflow-y                 : scroll;
        -webkit-overflow-scrolling : touch;
        &::-webkit-scrollbar {
          -webkit-appearance : none;
        }
        &::-webkit-scrollbar:vertical {
          width : $scroll-bar-width;
        }
        > * {
          width : $app-aside-width - 100;
        }
      }
      .smart & .navi-wrap {
        width : $app-aside-width;
      }
    }
    &.app-aside-folded {
      .app-aside {
        position : fixed;
        top      : 0;
        bottom   : 0;
        z-index  : 1010;
      }
      .aside-wrap {
        width : $app-aside-folded-width - 1;
        .navi-wrap {
          width : $app-aside-folded-width + $scroll-bar-width;
          > * {
            width : $app-aside-folded-width;
          }
        }
        .smart & .navi-wrap {
          width : $app-aside-folded-width;
        }
      }
    }
  }

  .bg-auto {
    &:before {
      content          : '';
      position         : absolute;
      width            : inherit;
      top              : 0;
      bottom           : 0;
      z-index          : -1;
      background-color : inherit;
      border           : inherit;
    }
    &.b-l:before {
      margin-left : -1px;
    }
    &.b-r:before {
      margin-right : -1px;
    }
  }

  .col.show {
    display : table-cell !important;
  }
}

// sm
@media (min-width : 768px) and (max-width : 991px) {
  .hbox-auto-sm {
    display : block;
    > .col {
      width   : auto;
      height  : auto;
      display : block;
      &.show {
        display : block !important;
      }
    }
  }
}

// xs
@media (max-width : 767px) {
  .app-aside {
    float : none;
  }

  .app-content-full {
    width : 100% !important;
  }

  .hbox-auto-xs {
    display : block;
    > .col {
      width   : auto;
      height  : auto;
      display : block;
    }
  }

  .navbar-nav {
    margin-top    : 0;
    margin-bottom : 0;
    > li > a {
      box-shadow : 0 -1px 0 rgba(0, 0, 0, 0.1);
      .up {
        top : 0;
      }
      .avatar {
        width      : 30px;
        margin-top : -5px;
      }
    }
    .open .dropdown-menu {
      background-color : #ffffff;
    }
  }

  .navbar-form {
    box-shadow    : 0 -1px 0 rgba(0, 0, 0, 0.1);
    margin-top    : 0 !important;
    margin-bottom : 0 !important;
    .form-group {
      margin-bottom : 0;
    }
  }
}

.aside-wrap-main {
  position         : fixed;
  overflow         : hidden;
  top              : 0;
  bottom           : 0;
  left             : 0;
  width            : 100px;
  z-index          : 1050;
  background-color : #1d2531;
  li {
    a {
      .icon,
      i {
        font-size : 2em;
        display   : block;
        margin    : 0 auto !important;
        float     : none !important;
      }
    }
  }
}

.app-header {
  min-height : $app-header-height;
}

#dashboard-alerts {
  position   : fixed;
  right      : 1rem;
  top        : 1rem;
  z-index    : 1100;
  text-align : right;
  .alert {
    display       : inline-block;
    padding       : 1rem;
    margin-bottom : 0.5rem;
    .close {
      padding : 0 0 0 1rem;
    }
  }
}



