  > .hover-action {
    display : inherit;
  }
  > .hover-rotate {
    @include rotate(90deg);
  }
}

.backdrop {
  position : absolute;
  top      : 0;
  right    : 0;
  bottom   : 0;
  left     : 0;
  z-index  : 1050;
  &.fade {
    opacity : 0;
    filter  : alpha(opacity=0);
  }
  &.in {
    opacity : 0.8;
    filter  : alpha(opacity=80);
  }
